/* Generates mutilevel nested comments
level-1 is the first level comment with a margin of 50px
level-2 is the first level comment with a margin of 100px
and so on...
*/
.social-media-block {
  z-index: 3; }
  .social-media-block .social-media-embed.aligncenter {
    text-align: center; }
    .social-media-block .social-media-embed.aligncenter > * {
      margin-left: auto !important;
      margin-right: auto !important; }

.submenu-block {
  background-color: rgba(255, 255, 255, 0.9);
  box-shadow: 5px 5px 5px 0 rgba(128, 128, 128, 0.5); }
  .submenu-block h1,
  .submenu-block h2 {
    padding-left: 16px;
    color: #666;
    font-family: "Roboto", sans-serif; }
  .submenu-block .submenu-menu {
    display: flex;
    flex-wrap: wrap; }
    .submenu-block .submenu-menu ul.submenu-item {
      flex-basis: 100%;
      padding: 0; }
      @media (min-width: 992px) {
        .submenu-block .submenu-menu ul.submenu-item {
          flex-basis: 50%; } }
    .submenu-block .submenu-menu a {
      color: #014c8c;
      text-decoration: none; }
    .submenu-block .submenu-menu ul {
      margin: 0.75em 0;
      list-style: none;
      padding: 0 16px; }
    .submenu-block .submenu-menu li {
      padding-left: 16px; }
      .submenu-block .submenu-menu li.list-style-bullet::before {
        content: "";
        background-image: url(data:image/svg+xml;base64,);
        background-position: 50% 50%;
        background-size: 1rem 1rem;
        background-repeat: no-repeat;
        margin-right: 2px;
        height: 1rem;
        width: 1rem;
        margin-bottom: -2px;
        display: inline-block; }
        @supports (mask-repeat: no-repeat) or (-webkit-mask-repeat: no-repeat) {
          .submenu-block .submenu-menu li.list-style-bullet::before {
            background-image: none;
            background-color: #014c8c;
            -webkit-mask-image: url(data:image/svg+xml;base64,);
            mask-image: url(data:image/svg+xml;base64,);
            mask-repeat: no-repeat;
            -webkit-mask-repeat: no-repeat;
            margin-right: 0; } }
      .submenu-block .submenu-menu li.list-style-number {
        list-style: decimal;
        margin-left: 20px; }
  .submenu-block.submenu-short .submenu-menu ul.submenu-item {
    flex-basis: 100%;
    column-count: 3; }
    .submenu-block.submenu-short .submenu-menu ul.submenu-item li:before {
      content: ""; }
  .submenu-block.submenu-long .submenu-menu ul.submenu-item {
    flex-basis: 100%;
    column-count: 2; }
  .submenu-block.submenu-sidebar {
    z-index: 4; }
    @media (min-width: 768px) {
      .submenu-block.submenu-sidebar {
        float: right;
        max-width: 350px;
        margin: 0 0 15px 15px; } }
    .submenu-block.submenu-sidebar .submenu-menu ul.submenu-item {
      flex-basis: 100%; }
      @media (min-width: 992px) {
        .submenu-block.submenu-sidebar .submenu-menu ul.submenu-item {
          flex-basis: 100%; } }

.back-top,
div.page-template > .back-top {
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  position: fixed;
  height: 44px;
  width: 44px;
  left: calc(85% - 26px);
  bottom: 50px;
  background-color: #074365;
  text-align: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25);
  display: none;
  z-index: 1000; }
  @media (min-width: 1200px) {
    .back-top,
    div.page-template > .back-top {
      height: 60px;
      width: 60px; } }
  .back-top:hover,
  div.page-template > .back-top:hover {
    text-decoration: none; }
